@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;600;700&family=Inter:wght@400;500;600;700&family=Nunito:wght@300;400;500;600;700;800&display=swap');


.container{
  
  border: 1px solid #C0C0C0;
  box-sizing: border-box;
  width: 446px;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 10px;
  z-index: 55;
  transform: translate(-50%, -50%);
  -webkit-box-shadow: 2px 1px 14px 0px rgba(27, 37, 45, 0.2);
-moz-box-shadow: 2px 1px 14px 0px rgba(27, 37, 45, 0.2);
box-shadow: 2px 1px 14px 0px rgba(27, 37, 45, 0.2);


 
}

.autorization{
  padding: 20px 40px 30px;
  &_div{
    margin-bottom: 18px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    
    >h2{
      font-family: Inter;
      font-style: normal;
      font-weight: bold;
      font-size: 26px;
      line-height: 110%;
      letter-spacing: -0.5px;
      color: #373737;
    }
    &_icon{
      font-size: 30px;
    }
  }
  &_form{
    >label{

      >p{
        
        font-family: Inter;
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 100%;
        color: #373737;
        margin: 20px 0px 5px 0px;
        padding: 0px 0px 2px 0px;
      }

      
      >input{
        
        width: 100%;
        border: 1px solid #EEEEEE;
        box-sizing: border-box;
        border-radius: 12px;
        font-family: Inter;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 18px;
        color: #373737;
        padding: 10px;
      }
    }

    
  }

}
.input_submit{
  margin-top: 20px;
  width: 366px;
  height: 49px;
  background: #FFDF36;
  border-radius: 12px;
  align-items: center;
  text-align: center;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #68572D;
  &:hover{
    box-shadow: 0 0 0 60px rgb(255, 208, 0) inset;
    
  }
  
}

.error{
  color: red;
  font-weight: 600;
  font-size: 10px;
}



